import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router, NavigationEnd } from '@angular/router';
import { QuizService } from '../dashboard/services/quiz.service';
import { Quiz } from '../dashboard/services/quiz';
import { User } from '../core/user';
import * as firebase from 'firebase/app';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';

@Component({
  selector: 'app-quiz-test',
  templateUrl: './quiz-test.component.html',
  styleUrls: ['./quiz-test.component.css'],
  providers: [QuizService]
})
export class QuizTestComponent implements OnInit, OnDestroy {

  usersList: AngularFireList<any>;
  users: AngularFireList<any>;
  user: User = new User;
  tempUser: User = new User;
  quizElement: Quiz = new Quiz;
  right: number;
  errorMessage = '';
  counter = 0;
  userPoints = 0;
  userLevel = 0;
  nextQuiz = '';
  answerButton = false;
  navigationSubscription;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private quizService: QuizService,
    private afDb: AngularFireDatabase
  ) {
    this.navigationSubscription = this.router.events.subscribe((e: any) => {
      if (e instanceof NavigationEnd) {
        this.initEverything();
      }
    });
  }

  ngOnInit() {
    this.init();
  }

  ngOnDestroy() {
    if (this.navigationSubscription) {
      this.navigationSubscription.unsubscribe();
    }
  }

  initEverything() {
    this.user = new User;
    this.tempUser = new User;
    this.quizElement = new Quiz;
    this.right = 0;
    this.errorMessage = '';
    this.counter = 0;
    this.userPoints = 0;
    this.userLevel = 0;
    this.nextQuiz = '';
    this.answerButton = false;
    this.init();
  }

  init() {
    this.usersList = this.afDb.list('users');
    const urlKey = this.route.snapshot.params['key'];
    this.getSingleQuiz(urlKey);
    this.retriveData();
  }

  getSingleQuiz(urlKey: String) {
    const x = this.quizService.getData();
    x.snapshotChanges().subscribe(item => {
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['key'] = element.key;
        if (element.key === urlKey) {
          this.quizElement = y as Quiz;
        }
      });
    });
  }

  checkAnswer(answer: String, x: number, quiz: Quiz) {
    if (answer === this.quizElement.rightAnswer) {
      this.right = x;
      if (this.counter > 0) {
        this.errorMessage = 'That is the right answer! You have already answered correctly.';
      } else {
        if (quiz.nextQuiz !== 'End') {
          this.answerButton = true;
        }
        this.errorMessage = 'Well done! You won ' + quiz.points + ' points!';
        this.updatePointsAndLevel(this.user, quiz.points, quiz.level);
        this.counter++;
      }
    } else {
      this.errorMessage = 'You should read the learning material of level ' + quiz.level + '. Do not give up!';
    }
  }

  retriveData() {
    this.users = this.getData();
    const user = firebase.auth().currentUser;
    this.users.snapshotChanges().subscribe(item => {
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['uid'] = element.key;
        if (element.key === user.uid) {
          this.user = y as User;
        }
      });
    });
  }

  getData() {
    this.usersList = this.afDb.list('users');
    return this.usersList;
  }

  updatePointsAndLevel(user: User, points, level) {
    this.userPoints = user.points;
    this.userLevel = user.level;
    const newPoints = parseInt(points, 10) + this.userPoints;
    let newLevel = parseInt(level, 10);
    if (newLevel < this.userLevel) {
      newLevel = this.userLevel;
    }
    this.usersList.update(user.uid, {
      points: newPoints,
      level: newLevel
    });
  }
}
