import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';
import { Material } from './material';

@Injectable()
export class MaterialService {

  materialList: AngularFireList<any>;
  selectedMaterial: Material = new Material();

  constructor(
    private firebase: AngularFireDatabase
  ) {
    this.materialList = this.firebase.list('material');
  }

  getData() {
    this.materialList = this.firebase.list('material');
    return this.materialList;
  }

  insertMaterial(material: Material) {
    this.materialList.push({
      title: material.title,
      text1: material.text1,
      text2: material.text2,
      text3: material.text3,
      text4: material.text4,
      text5: material.text5,
      image1: material.image1,
      image2: material.image2,
      image3: material.image3,
      image4: material.image4,
      image5: material.image5,
      level: material.level
    });
  }

  updateMaterial(newMaterial: Material) {
    this.materialList.update(newMaterial.key, {
      title: newMaterial.title,
      text1: newMaterial.text1,
      text2: newMaterial.text2,
      text3: newMaterial.text3,
      text4: newMaterial.text4,
      text5: newMaterial.text5,
      image1: newMaterial.image1,
      image2: newMaterial.image2,
      image3: newMaterial.image3,
      image4: newMaterial.image4,
      image5: newMaterial.image5,
      level: newMaterial.level
    });
  }

  deleteMaterial(key: string) {
    this.materialList.remove(key);
  }

}
