import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { AngularFireModule } from '@angular/fire';
import { AngularFirestoreModule } from '@angular/fire/firestore';
import { AngularFireDatabaseModule } from '@angular/fire/database';
import { AngularFireAuthModule } from '@angular/fire/auth';
import { environment } from '../environments/environment';
import { AppRoutes } from './app.routing';
import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { QuizLevelsComponent } from './quiz-levels/quiz-levels.component';
import { MaterialTileComponent } from './quiz-levels/material-tile/material-tile.component';
import { QuizTileComponent } from './quiz-levels/quiz-tile/quiz-tile.component';
import { QuizTestComponent } from './quiz-test/quiz-test.component';
import { MaterialComponent } from './material/material.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { QuizFormComponent } from './dashboard/quiz-form/quiz-form.component';
import { QuizListComponent } from './dashboard/quiz-list/quiz-list.component';
import { MaterialFormComponent } from './dashboard/material-form/material-form.component';
import { MaterialListComponent } from './dashboard/material-list/material-list.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { InformationComponent } from './information/information.component';
import { UserComponent } from './dashboard/user/user.component';
import { StatisticsComponent } from './dashboard/statistics/statistics.component';
import { AuthService } from './core/auth.service';
import { AuthGuard } from './core/auth.guard';

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    DashboardComponent,
    QuizFormComponent,
    QuizListComponent,
    HeaderComponent,
    FooterComponent,
    QuizLevelsComponent,
    MaterialComponent,
    MaterialTileComponent,
    QuizTileComponent,
    MaterialListComponent,
    MaterialFormComponent,
    QuizTestComponent,
    LoginComponent,
    RegisterComponent,
    InformationComponent,
    UserComponent,
    StatisticsComponent
  ],
  imports: [
    BrowserModule,
    AngularFireModule.initializeApp(environment.firebase),
    AngularFirestoreModule,
    AngularFireAuthModule,
    AngularFireDatabaseModule,
    AngularFireModule,
    ReactiveFormsModule,
    FormsModule,
    RouterModule.forRoot(
      AppRoutes,
      { onSameUrlNavigation: 'reload', enableTracing: true }
    )
  ],
  providers: [AuthService, AuthGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }
