import { Routes } from '@angular/router';
import { QuizLevelsComponent } from './quiz-levels/quiz-levels.component';
import { QuizTestComponent } from './quiz-test/quiz-test.component';
import { MaterialComponent } from './material/material.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { InformationComponent } from './information/information.component';
import { AuthGuard } from './core/auth.guard';

export const AppRoutes: Routes = [
    { path: '', component: LoginComponent },
    { path: 'register', component: RegisterComponent },
    { path: 'quiz-levels', component: QuizLevelsComponent, canActivate: [AuthGuard] },
    { path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard] },
    { path: 'information', component: InformationComponent, canActivate: [AuthGuard] },
    { path: 'quiz-test/:key', component: QuizTestComponent, canActivate: [AuthGuard], runGuardsAndResolvers: 'always' },
    { path: 'material/:key', component: MaterialComponent, canActivate: [AuthGuard], runGuardsAndResolvers: 'always' }
];
