import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';
import { Quiz } from './quiz';

@Injectable()
export class QuizService {

  quizList: AngularFireList<any>;
  selectedQuiz: Quiz = new Quiz();
  constructor(
    private firebase: AngularFireDatabase
  ) {
    this.quizList = this.firebase.list('quizzes');
  }

  getData() {
    this.quizList = this.firebase.list('quizzes');
    return this.quizList;
  }

  insertQuiz(quiz: Quiz) {
    this.quizList.push({
      question: quiz.question,
      answer1: quiz.answer1,
      answer2: quiz.answer2,
      answer3: quiz.answer3,
      answer4: quiz.answer4,
      rightAnswer: quiz.rightAnswer,
      points: quiz.points,
      level: quiz.level,
      nextQuiz: quiz.nextQuiz
    });
  }

  updateQuiz(newQuiz: Quiz) {
    this.quizList.update(newQuiz.key, {
      question: newQuiz.question,
      answer1: newQuiz.answer1,
      answer2: newQuiz.answer2,
      answer3: newQuiz.answer3,
      answer4: newQuiz.answer4,
      rightAnswer: newQuiz.rightAnswer,
      points: newQuiz.points,
      level: newQuiz.level,
      nextQuiz: newQuiz.nextQuiz
    });
  }

  deleteQuiz(key: string) {
    this.quizList.remove(key);
  }

}
