import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { MaterialService } from '../dashboard/services/material.service';
import { Material } from '../dashboard/services/material';

@Component({
  selector: 'app-material',
  templateUrl: './material.component.html',
  styleUrls: ['./material.component.css'],
  providers: [MaterialService]
})
export class MaterialComponent implements OnInit {
  materialList: Material[];
  materialElement: Material;

  constructor(
    private route: ActivatedRoute,
    private materialService: MaterialService,
    private location: Location
  ) { }

  ngOnInit() {
    const urlKey = this.route.snapshot.params['key'];
    this.getSingleMaterial(urlKey);
  }

  getSingleMaterial(urlKey: string): void {
    const x = this.materialService.getData();
    x.snapshotChanges().subscribe(item => {
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['key'] = element.key;
        if (element.key === urlKey) {
          this.materialElement = y as Material;
        }
      });
    });
  }
}
