import { Component, OnInit } from '@angular/core';
import * as firebase from 'firebase/app';
import { User } from '../../core/user';
import { NgForm } from '@angular/forms';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {

  users: AngularFireList<any>;
  successMessage = '';
  user: User = new User;
  tempUser: User = new User;

  constructor(
    private afDb: AngularFireDatabase
  ) {
    this.user.providerId = '';
    this.user.uid = '';
    this.user.displayName = '';
    this.user.email = '';
    this.user.photoURL = 'assets/img/portfolio/avatar.png';
    this.user.points = 0;
    this.user.level = 0;
  }

  ngOnInit() {
    this.retrivePointsAndLevel();
    this.resetForm();
  }

  getData() {
    this.users = this.afDb.list('users');
    return this.users;
  }

  retrivePointsAndLevel() {
    const x = this.getData();
    const id = this.initData();
    x.snapshotChanges().subscribe(item => {
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['uid'] = element.key;
        if (element.key === id) {
          this.tempUser = y as User;
        }
      });
    });
  }

  initData(): string {
    const user = firebase.auth().currentUser;
    let providerId, displayName, email, photoUrl;

    if (user != null) {
      this.user.uid = user.uid;
      user.providerData.forEach(function (profile) {
        providerId = profile.providerId;
        displayName = profile.displayName;
        email = profile.email;
        photoUrl = profile.photoURL;
      });
      this.user.providerId = providerId;
      if (displayName) {
        this.user.displayName = displayName;
      }
      if (photoUrl) {
        this.user.photoURL = photoUrl;
      }
      this.user.email = email;
      return this.user.uid;
    } else {
      return null;
    }
  }

  resetForm(form?: NgForm) {
    if (form != null) {
      form.reset();
    }
  }

  onSubmit(form: NgForm) {
    const user = firebase.auth().currentUser;
    if (form.value.key == null) {
      user.updateProfile({
        displayName: form.value.displayName,
        photoURL: form.value.photoURL
      }).then(function () {
        console.log('Success');
      }).catch(function (error) {
        console.log(error);
      });
      this.successMessage = 'Successfully updated!';
    } else {
      this.resetForm(form);
    }
  }
}
