import { Injectable } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';
import * as firebase from 'firebase/app';
import { User } from './user';

@Injectable()
export class AuthService {

  usersList: AngularFireList<any>;
  user: User = new User;

  constructor(
    public afAuth: AngularFireAuth,
    private afDb: AngularFireDatabase
  ) { }

  doFacebookLogin() {
    return new Promise<any>((resolve, reject) => {
      const provider = new firebase.auth.FacebookAuthProvider();
      this.afAuth.auth
        .signInWithPopup(provider)
        .then(res => {
          resolve(res);
          const prov = firebase.auth().currentUser;
          this.writeUserDataGF(prov);
        }, error => {
          console.log(error);
          reject(error);
        });
    });
  }

  doGoogleLogin() {
    return new Promise<any>((resolve, reject) => {
      const provider = new firebase.auth.GoogleAuthProvider();
      this.afAuth.auth
        .signInWithPopup(provider)
        .then(res => {
          resolve(res);
          const prov = firebase.auth().currentUser;
          this.writeUserDataGF(prov);
        }, error => {
          console.log(error);
          reject(error);
        });
    });
  }

  writeUserDataGF(provider) {
    const newUser = firebase.database().ref('users/' + provider.uid);
    newUser.transaction(function (currentData) {
      if (currentData === null) {
        return {
          displayName: provider.displayName,
          email: provider.email,
          photoURL: provider.photoURL,
          loginProvider: 'google-facebook',
          userRole: 'student',
          points: 0,
          level: 0
        };
      } else {
        console.log('User already exists.');
        return;
      }
    }, function (error, committed, snapshot) {
      if (error) {
        console.log('Transaction failed abnormally!', error);
      } else if (!committed) {
        console.log('We aborted the transaction (because user already exists).');
      } else {
        console.log('User added!');
      }
      console.log('Data of user: ', snapshot.val());
    });
  }

  doRegister(value) {
    return new Promise<any>((resolve, reject) => {
      firebase.auth().createUserWithEmailAndPassword(value.email, value.password)
        .then(res => {
          resolve(res);
          const userId = firebase.auth().currentUser.uid;
          this.writeUserData(userId, value.email);
        }, err => reject(err));
    });
  }

  writeUserData(userId, email) {
    firebase.database().ref('users/' + userId).set({
      displayName: '',
      email: email,
      photoURL: 'assets/img/portfolio/avatar.png',
      loginProvider: 'anonymous',
      userRole: 'student',
      points: 0,
      level: 0
    });
  }

  doLogin(value) {
    return new Promise<any>((resolve, reject) => {
      firebase.auth().signInWithEmailAndPassword(value.email, value.password)
        .then(res => {
          resolve(res);
        }, err => reject(err));
    });
  }

  doLogout() {
    return new Promise((resolve, reject) => {
      if (firebase.auth().currentUser) {
        this.afAuth.auth.signOut();
        resolve();
      } else {
        reject();
      }
    });
  }
}
